import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import {
  redirectUnauthorizedTo,
  redirectLoggedInTo,
  canActivate,
} from '@angular/fire/auth-guard';
const redirectUnauthorizedToLogin = () => redirectUnauthorizedTo(['']);
const redirectLoggedInToHome = () => redirectLoggedInTo(['gurukul']);

const routes: Routes = [
  {
    path: '',
    loadChildren: () =>
      import('./pages/auth/login/login.module').then((m) => m.LoginPageModule),
    ...canActivate(redirectLoggedInToHome),
  },
  {
    path: 'gurukul',
    loadChildren: () =>
      import('./pages/gurukul/dashboard/dashboard.module').then((m) => m.DashboardPageModule),
    ...canActivate(redirectUnauthorizedToLogin),
  },
  {
    path: 'courses',
    loadChildren: () => import('./pages/gurukul/courses/courses.module').then(m => m.CoursesPageModule),
    ...canActivate(redirectUnauthorizedToLogin),
  },
  {
    path: 'students',
    loadChildren: () => import('./pages/gurukul/students/students.module').then(m => m.StudentsPageModule),
    ...canActivate(redirectUnauthorizedToLogin),
  },
  {
    path: 'answer-sheets',
    loadChildren: () => import('./pages/gurukul/answer-sheets/answer-sheets.module').then(m => m.AnswerSheetsPageModule),
    ...canActivate(redirectUnauthorizedToLogin),
  },
  {
    path: 'account',
    loadChildren: () => import('./pages/gurukul/accounts/accounts.module').then(m => m.AccountsPageModule),
    ...canActivate(redirectUnauthorizedToLogin),
  },
  {
    path: 'orders',
    loadChildren: () => import('./pages/gurukul/orders/orders.module').then(m => m.OrdersPageModule),
    ...canActivate(redirectUnauthorizedToLogin),
  },
  {
    path: 'tests',
    loadChildren: () => import('./pages/gurukul/tests/tests.module').then(m => m.TestsPageModule),
    ...canActivate(redirectUnauthorizedToLogin),
  },
  {
    path: 'test',
    loadChildren: () => import('./pages/gurukul/test/test.module').then(m => m.TestPageModule),
    ...canActivate(redirectUnauthorizedToLogin),
  },
  {
    path: 'notifications',
    loadChildren: () => import('./pages/gurukul/notifications/notifications.module').then(m => m.NotificationsPageModule),
    ...canActivate(redirectUnauthorizedToLogin),
  },
  {
    path: 'add-test',
    loadChildren: () => import('./pages/gurukul/add-test/add-test.module').then(m => m.AddTestPageModule),
    ...canActivate(redirectUnauthorizedToLogin),
  },
  {
    path: 'registered-students',
    loadChildren: () => import('./pages/gurukul/registered-students/registered-students.module').then(m => m.RegisteredStudentsPageModule),
    ...canActivate(redirectUnauthorizedToLogin),
  },
  {
    path: 'chat-room',
    loadChildren: () => import('./pages/gurukul/chat-room/chat-room.module').then(m => m.ChatRoomPageModule),
    ...canActivate(redirectUnauthorizedToLogin),
  },
  {
    path: 'chat',
    loadChildren: () => import('./pages/gurukul/chat/chat.module').then(m => m.ChatPageModule),
    ...canActivate(redirectUnauthorizedToLogin),
  },
  {
    path: '**',
    redirectTo: '',
    pathMatch: 'full',
  },
];
@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
